/*! Defaults */
body {
  color: black;
  font-family: Arial, sans-serif;
  font-size: 1rem;
}

body::-webkit-scrollbar {
  width: 0;
}

p::selection {
  background: #5DE0B1;
  color: #fff;
}

span::selection {
  background: #5DE0B1;
  color: #fff;
}

h3::selection {
  background: #5DE0B1;
  color: #fff;
}

h2::selection {
  background: #5DE0B1;
  color: #fff;
}

a::selection {
  background: #5DE0B1;
  color: #fff;
}

li::selection {
  background: #5DE0B1;
  color: #fff;
}

h1::selection {
  background: #5DE0B1;
  color: #fff;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

/*! Defaults End */
/*! .header */
.header {
  transition: all linear .5s;
  height: 70px;
  width: 100%;
  background: #ffffff;
  position: fixed;
  z-index: 9999;
}

.header__container {
  transition: all linear .5s;
  margin: 0 70px;
}

.line {
  background: #000000;
  width: 6px;
  height: 2px;
  margin: 1px 0;
}

.header__container-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.header__container-content-logo {
  background: #FFFFFF;
  box-shadow: 0px 0px 4px rgba(154, 151, 151, 0.25);
  padding: 23px 22px;
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -125px;
}

.header__container-content-logo a img {
  transition: all linear .5s;
}

.header__container-content-menu {
  display: flex;
  align-items: center;
}

.header__container-content-menu {
  transition: all linear .5s;
}

.header__container-content-menu a {
  text-decoration: none;
  transition: all linear .5s;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #000000;
}

.header__container-content-menu a:hover {
  color: #5DE0B1;
}

.burger {
  width: 40px;
  align-items: center;
  justify-content: center;
  height: 70px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  padding-left: 1.25em;
  position: relative;
  top: 15px;
}

.line {
  display: block;
  background: #000000;
  width: 20px;
  height: 2px;
  margin: 2px 0;
  backface-visibility: hidden;
  position: absolute;
  left: 0;
  border-top: 2px solid #000;
  transition: 0.55s;
}

.menu-top {
  top: 11px;
}

.menu-middle {
  top: 18px;
}

.menu-bottom {
  top: 25px;
}

.menu-top-click {
  backface-visibility: hidden;
  top: 15px;
  transform: rotate(50deg);
  transition: 0.55s 0.5s;
}

.menu-middle-click {
  opacity: 0;
}

.menu-bottom-click {
  backface-visibility: hidden;
  top: 15px;
  transform: rotate(-410deg);
  transition: 0.55s 0.5s;
}

.lang-items {
  cursor: pointer;
}

.lang-items a {
  transition: all linear .5s;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px;
  text-align: center;
  color: #3E413D;
  padding: 26px 28px;
}

.lang-items a:hover {
  text-decoration: none;
  background: #F0FCF6;
}

.active {
  background: #F0FCF6;
}

.main-menu__opened-container {
  margin: 0 70px;
}

.main-menu__opened {
  overflow-y: auto;
  transition: all linear .5s;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 60px 0;
  background: #fff;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(4px);
  z-index: 999;
}

.content-menu__nav ul {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.content-menu__nav ul li {
  cursor: pointer;
  width: 250px;
  margin: 16px 0;
  padding: 12px 0;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #3E413D;
  border-bottom: 2px solid #5DE0B1;
  transition: all linear .5s;
}

.content-menu__nav ul li:hover {
  margin-left: 15px;
  color: #5DE0B1;
  border-bottom: 2px solid #3E413D;
}

.content-menu {
  display: flex;
  justify-content: space-between;
}

.content-menu__contacts {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.content-menu__contacts h3 {
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #3E413D;
}

.content-menu__contacts p {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  display: flex;
  align-items: center;
  color: #2D2F2D;
}

.green-links {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  display: flex;
  align-items: center;
  text-decoration-line: underline;
  color: #5DE0B1 !important;
  cursor: pointer;
}

.social-links {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.social-links a {
  margin-right: 15px;
}

.lang-items-mob {
  display: none;
  align-items: center;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  text-align: center;
  color: #3E413D;
}

.lang-items-mob a {
  margin-right: 5px;
  color: #3E413D;
}

.popup-window {
  background-image: url("../img/popup-bg.png");
  background-size: cover;
  width: 100%;
  height: 100%;
  min-height: 800px;
}

.popup-window__form {
  transition: all linear .5s;
  width: 412px;
  height: 272px;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #FFFFFF;
  border-radius: 2px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.popup-window__form h4 {
  margin: 30px 0;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 150%;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}

.popup-row {
  background: transparent;
  border: none;
  border-bottom: 1px solid #5DE0B1;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 150%;
  text-align: left;
  color: #959595;
  margin: 20px 0;
  width: 70%;
}

.popup-button {
