.main-page__content {
    width: 100%;
    height: 100%;
    margin: 100px 0;
    transition: all ease-in .5s; }

.main-page__content-container {
    margin: 0 70px;
    transition: all ease-in .5s; }
.breadcrumbs {
    margin: 58px 0;
    transition: all ease-in .5s;
    a {
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        display: flex;
        align-items: center;
        color: #000000; } }

.main-title {
    display: flex;
    justify-content: left;
    transition: all ease-in .5s; }
.hidden {
    display: block!important; }
.mobile-title {
    display: none; }
.title-part {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: all ease-in .5s;
    margin: 0 110px; }
.category-name {
    p {
        font-style: normal;
        font-weight: normal;
        font-size: 15px;
        line-height: 150%;
        display: flex;
        align-items: center;
        color: #959595; } }

.post-name {
    h3 {
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 29px;
        display: flex;
        align-items: center;
        text-transform: uppercase;
        color: #5DE0B1; } }


.post-date {
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 150%;
    display: flex;
    align-items: center;
    color: #959595; }

.main-content {
    width: 64%;
    p {
        margin: 53px 0;
        font-style: normal;
        font-weight: normal;
        font-size: 15px;
        line-height: 150%;
        color: #000000; } }
