// ====================  Default  =========================


/*! Defaults */
body {
  color: $main-color;
  font-family: $default-font;
  @include font-size(16px);
  &::-webkit-scrollbar {
    width: 0; } }

p {
  &::selection {
    background:#5DE0B1 {}
    color: #fff; } }
span {
  &::selection {
    background:#5DE0B1 {}
    color: #fff; } }
h3 {
  &::selection {
    background:#5DE0B1 {}
    color: #fff; } }
h2 {
  &::selection {
    background:#5DE0B1 {}
    color: #fff; } }
a {
  &::selection {
    background:#5DE0B1 {}
    color: #fff; } }
li {
  &::selection {
    background:#5DE0B1 {}
    color: #fff; } }

h1 {
  &::selection {
    background:#5DE0B1 {}
    color: #fff; } }
// sticky footer (always at the bottom of page)

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
 }  // height: 100%


.content {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

