// =====================  Media  ==========================

/*! Media */
@media screen and (min-width: 1450px) {
  .stories-screen__item {
    height: 700px; }
  .line-green {
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%); }

  .content {
    height: 800px; }
  .stories-screen__item {
    width: 27%; }
  .stories-screen__container {
    padding: 0 70px;
    justify-content: center; }
  .projects-main__content {
    justify-content: center; }
  .preject__item {
    max-width: 600px;
    margin: 10px; }
  .projects-main__types {
    ul {
      justify-content: space-evenly; } } }
@media screen and (max-width: 1200px) {
  .slider-pojects__item {
    position: relative;
    width: 32%; }
  .projects-items {
    width: 100%;
    min-height: 360px; }
  .project-item {
    span {
      padding: 4px 9px; } }
  .priorities-container__item {
    transition: all linear .5s;
    width: 28%; }

  .line-green {
    background: #5DE0B1!important;
    width: 184px;
    height: 4px;
    display: block;
    left: 27%; }
  .top {
    top: 32px; }
  .bottom {
    bottom: 32px; }

  .image-color {
    transition: all linear .5s;
    filter: grayscale(100%);
    &:hover {
      filter: grayscale(0); } }

  .story-title {
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 43px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #FFFFFF;
    top: 45%;
    left: 32%;
    &::before,
    &::after {
      content: '';
      position: absolute;
      background: #5DE0B1!important;
      width: 55px;
      height: 4px;
      display: block; }
    &::before {
      left: -96px;
      top: 20px; }
    &::after {
      right: -96px;
      top: 20px; }
    .preject__item {
      max-width: 388px; }
    .preject__item {
      img {
        width: 100%; } } } }

@media screen and (max-width: 992px) {
  .priorities-title {
    h2 {
      font-size: 44px; } }
  .priorities-container__item {
    width: 33%; }
  .story-title {
    font-size: 28px; }
  .stories-screen__item {
    max-height: 514px; }
  .preject__item {
    h3 {
      font-size: 18px; } }
  .content-menu__contacts {
    justify-content: space-around; }
  .social-links {
    padding: 36px 0; }
  .footer-content-title {
    width: 100%; } }
@media screen and (max-width: 768px) {
  .slider-pojects__item {
    width: 56%; }

  .project-item {
    span {
      font-size: 14px; } }
  .project-item {
    h4 {
      font-size: 19px; } }
  .projects-items {
    width: 100%;
    min-height: 295px; }
  // .projects-items
  //   flex-direction: column
  .project-item {
    width: 40%; }
  .partners__items {
    ul {
      flex-wrap: wrap;
      li {
        width: 50%; } } }
  .priorities-title {
    p {
      margin: 30px 0; } }
  .about-screen__text {
    width: 100%; }
  .about-screen__image {
    display: none; }
  .stories-screen__container {
    height: 242vh;
    align-items: center; }
  .stories-screen__item {
    width: 50%; }
  .priorities-container__item {
    width: 50%; }
  .priorities-container__items {
    flex-direction: column; }

  .stories-screen__container {
    flex-direction: column; }
  .title-part {
    margin: 0 18px; }
  .main-content {
    width: 100%; }
  .header__container-content-logo {
    margin-left: -100px; }
  .lang-items {
    display: none; }
  .lang-items-mob {
    display: flex; }
  .content-menu__contacts {
    display: none; }
  .header__container-content-logo {
    a {
      img {
        width: 165px; } } } }
