.content {
  background-image: url('../img/bg.png');
  background-size: cover;
  width: 100%;
  height: 527px;
  position: relative; }
.main-content__container {
  transition: all linear .5s;
  position: absolute;
  top: 50%;
  margin-top: -2em;
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  left: 20%;
  h1 {
    transition: all linear .5s;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 38px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #FFFFFF; }
  a {
    transition: all linear .5s;
    text-decoration: none;
    color: #fff; } }

.first-screen-button {

  text-decoration: none;
  margin: 30px 0; }

.about-screen {
  display: flex; }

.about-screen__text {
  width: 64%;
  padding: 24px 96px;
  background: #5DE0B1;
  p {
    padding: 20px 0;
    font-style: normal;
    font-weight: 600;
    font-size: 26px;
    line-height: 150%;
    color: #FFFFFF; } }

.about-screen__image {
  background-size: cover;
  width: 36%;
  transition: all linear .5s;
  background-image: url('../img/14.png');
  &:hover {
    background-image: url('../img/15.png'); } }
.stories-screen__container {
  position: relative;
  width: 100%;
  height: 700px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around; }
.stories-screen__item {
  width: 33.3%;
  height: 100%;
  max-height: 670px;
  position: relative;
  transition: all linear .5s;
  cursor: pointer;
  p {
    position: absolute; }

  span {
    position: absolute;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%); }
  img {
    width: 100%;
    height: 100%;
    position: absolute; } }
.stories-screen__title {
  position: relative;
  text-align: center;
  margin: 50px 0;
  h2 {
    font-style: normal;
    font-weight: normal;
    font-size: 56px;
    line-height: 67px;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #5DE0B1; } }

.all-story {
  position: absolute;
  right: 5vw;
  top: 25px;
  display: flex;
  transition: all linear .5s;
  img {
    transition: all linear .5s; }
  a {
    transition: all linear .5s;
    margin: 0 11px;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #2D2F2D;
    text-decoration: none;
    &:hover ~ img {
      margin-right: 20px; }
    &:hover {
      color: #5DE0B1; } } }

.line-green {
  background: #5DE0B1!important;
  width: 184px;
  height: 4px;
  display: block;
  left: 27%; }
.top {
  top: 32px; }
.bottom {
  bottom: 32px; }

.image-color {
  transition: all linear .5s;
  filter: grayscale(100%);
  &:hover {
    filter: grayscale(0); } }

.story-title {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 43px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #FFFFFF;
  top: 45%;
  left: 32%;
  &::before,
  &::after {
      content: '';
      position: absolute;
      background: #5DE0B1!important;
      width: 55px;
      height: 4px;
      display: block; }
  &::before {
    left: -96px;
    top: 20px; }
  &::after {
    right: -96px;
    top: 20px; } }


.all-story-mobile {
  display: none; }

.priorities-container {
  margin: 0 70px; }
.priorities-title {
  margin: 30px 0;
  h2 {
    font-style: normal;
    font-weight: normal;
    font-size: 56px;
    line-height: 67px;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #5DE0B1; }
  p {
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    color: #2D2F2D;
    margin: 30px 126px; } }

.item-green {
  padding: 40px 20px;
  background: #5DE0B1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  p {
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 150%;
    display: flex;
    align-items: center;
    text-align: center;
    color: #FFFFFF; } }
.priorities-container__item {
  width: 23%;
  transition: all linear .5s; }

.item-main {
  img {
    transition: all linear .5s;
    cursor: pointer;
    filter: grayscale(100%);
    width: 100%;
    &:hover {
       filter: grayscale(0); } } }

.item-main {
  margin-top: 24px;
  position: relative;
  transition: all linear .5s;
  p {
    position: absolute;
    z-index: 9;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%); }
  h4 {
    transition: all linear .5s;
    position: absolute;
    z-index: 9;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 150%;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #FFFFFF;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%); } }
.priorities-container__items {
  display: flex;
  align-items: center;
  justify-content: space-around;
  transition: all linear .5s; }

.item-button {
  margin: 20px 0;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
  a {
    color: #2D2F2D;
    text-decoration: none; } }
.green-line-block {
  transition: all linear .5s;
  display: block;
  width: 136px;
  height: 2px;
  background: #5DE0B1;
  &:hover {
    width: 90px; } }

.item-button {
  &:hover .green-line-block {
    width: 90px; } }

.priorities-title {
  position: relative; }


.project-item {
  position: relative;
  height: auto;
  width: 27%;

  p {
    position: absolute;
    z-index: 9;
    top: 20px;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%); }
  span {
    padding: 4px 28px;
    background:  #5DE0B1;
    position: absolute;
    bottom: 20px;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 150%;
    display: flex;
    align-items: center;
    text-align: center;
    color: #FFFFFF;
    text-align: center;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%); }
  h4 {
    z-index: 9;
    position: absolute;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #FFFFFF;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%); }
  img {
    width: 100%;
