.green-line {
  width: 184px;
  height: 2px;
  display: block;
  background: #5DE0B1;
  z-index: 9; }


.projects-items {
  width: 100%;
  min-height: 408px;
  display: flex;
  justify-content: space-between; }

.partners__items {
  width: 100%;
  margin: 20px 0;
  margin-bottom: 63px;
  ul {
    // display: flex
    // justify-content: space-between
    li {
      width: 25%;
      padding: 50px 0;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 22px;
      text-align: center;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      color: #000000;
      border-right: 2px solid #5DE0B1;
      &:last-child {
        border: none; } } } }

.projects__arrows {
  display: flex;
  align-items: center;
  justify-content: space-between;
