    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #FFFFFF;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%); }
  span {
    padding: 4px 28px;
    background:  #5DE0B1;
    position: absolute;
    bottom: 20px;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 150%;
    display: flex;
    align-items: center;
    text-align: center;
    color: #FFFFFF;
    text-align: center;
