/*! .footer */
.footer {
  width: 100%;
  height: auto;
  background: #5DE0B1;
  box-shadow: 0px -0.5px 1px rgba(170, 170, 170, 0.25);
  color: #fff; }

.footer-container {
  margin: 0 70px;
  padding: 20px 0;
  display: flex;
  align-items: start;
  justify-content: space-between; }

.footer-content-title {
  width: 45%;
  h4 {
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    display: flex;
    align-items: center;
    color: #FFFFFF; }
  p {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    display: flex;
    align-items: center; } }

.footer-content-social {
  width: 35%;
  display: flex;
  align-items: center;
  a {
    margin-right: 4px; } }

.footer-content-links {
  p {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    ine-height: 22px;
    display: flex;
    align-items: center;
    text-decoration-line: underline;
    color: #FFFFFF; } }
.first {
  margin-bottom: 30px; }

