/*! Nulling styles */

*, *::before, *::after {
  box-sizing: border-box; }

input, button, textarea {
  font-family: inherit; }

input::-ms-clear {
  display: none; }

a:focus, a:active, input, input:focus {
  outline: none; }

a, button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

img {
  vertical-align: top; }

html, body {
  width: 100%;
  height: 100%;
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

/*! Nulling styles End */
