.popup-window {
    background-image: url('../img/popup-bg.png');
    background-size: cover;
    width: 100%;
    height: 100%;
    min-height: 800px; }
.popup-window__form {
    transition: all linear .5s;
    width: 412px;
    height: 272px;
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: #FFFFFF;
    border-radius: 2px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    h4 {
        margin: 30px 0;
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 150%;
        text-align: center;
        text-transform: uppercase;
        color: #000000; } }
.popup-row {
    background: transparent;
    border: none;
    border-bottom: 1px solid#5DE0B1;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 150%;
    text-align: left;
    color: #959595;
    margin: 20px 0;
    width: 70%; }

.popup-button {
    background: #5DE0B1;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    border-radius: 2px;
    border: none;
    padding: 13px 34px; }
