.projects-main {
    width: 100%;
    height: auto;
    position: relative;
    padding: 70px 0; }

.projects-main__title {
    margin: 82px 0;
    text-align: center;
    display: flex;
    justify-content: center;
    h2 {
        font-style: normal;
        font-weight: 600;
        font-size: 56px;
        line-height: 67px;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.1em;
        color: #5DE0B1; } }

.projects-main__types {
    ul {
        display: flex;
        align-items: center;
        justify-content: space-around;
        li {
            transition: all linear .5s;
            cursor: pointer;
            padding: 30px;
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 150%;
            display: flex;
            align-items: center;
            text-align: center;
            color: #2D2F2D;
            &:hover {
                color: #5DE0B1; }
            &:active {
                color: #5DE0B1; }
            &:focus {
                color: #2D2F2D; } } } }

.projects-main__content {
    transition: all linear .5s;
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between; }


.preject__item {
    transition: all linear .5s;
    position: relative;
    cursor: pointer;
    margin-top: 20px;
    max-width: 463px;
    width: 90%;
    img {
        transition: all linear .5s;
        width: 100%; }
    h3 {
        transition: all linear .5s;
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 28px;
        display: flex;
        align-items: center;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        position: absolute;
        // top: 255px
        text-align: center;
        // left: 54%
        // margin-left: -167px
        top: 50%;
        left: 50%;
        margin-right: -50%;
        transform: translate(-50%, -50%); } }

